import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent {
  
  	private copper = {name: "Copper", type: cardType.treasure, value: 1, cost: 10};
  	private silver = {name: "Silver", type: cardType.treasure, value: 2, cost: 30};
  	private gold = {name: "Gold", type: cardType.treasure, value: 3, cost: 50};
  	private estate = {name: "Estate", type: cardType.victory, value: 1, cost: 20};
  	private province = {name: "Province", type: cardType.victory, value: 6, cost: 60};
  	private duchy = {name: "Duchy", type: cardType.victory, value: 3, cost: 50};
  	private village = {name: "Village", type: cardType.action, cost: 30};
  	private laboratory = {name: "Laboratory", type: cardType.action, cost: 50};
  	private smithy = {name: "Smithy", type: cardType.action, cost: 40};
  	private cards = [this.copper, this.silver, this.gold, this.estate, this.duchy, this.province, this.village, this.laboratory, this.smithy];
	private deck = [this.copper, this.copper, this.copper, this.copper, this.copper, this.copper, this.estate, this.estate, this.estate];
	private hand = [];
	private money = 0;
	private handSize = 5;
	private actions = 1;
	private prestiege = 0;
	private turnCount = 0;
	private turnActions = [];
	private victory = 3;
	private speed = 3000;
	private press = false;

	private shuffleArray(array: any[]) {
		for (let i = array.length - 1; i > 0; i--) {
	        const j = Math.floor(Math.random() * (i + 1));
	        [array[i], array[j]] = [array[j], array[i]];
  		}
  	}

  	private ngOnInit() {
  		setTimeout(() => this.newHand(), 0);
  	}

  	private getCardType(array, type: cardType): any[] {
  		return array.filter((card) => card.type === type);
  	}

  	private addValues(cards, init = 0): number {
  		return cards.map((card) => card.value)
  		.reduce((c1: number, c2: number) => c1 + c2, init);
  	}

  	private newHand() {
  		this.shuffleArray(this.deck);
  		this.turnActions = [];
  		this.turnCount++;
  		let end = this.handSize;
  		this.hand = this.deck.slice(0, end);

  		let actions = this.actions;
  		let treasures = 0;
  		for(let i = 0; i < this.hand.length; i++) {
  			if(actions < 1) break;
  			if(this.hand[i].type === cardType.action) {
  				actions--;
  				let action = this.hand[i];
  				this.turnActions.push("Played " + action.name);
  				if(action.name === "Village") {
  					end++;
  					actions += 2;
  				}
  				if(action.name === "Laboratory") {
  					end += 2;
  					actions++;
  				}
  				if(action.name === "Smithy") {
  					end += 3;
  				}
  				this.hand = this.deck.slice(0, end);
  			};
  		}
  		treasures += this.addValues(this.getCardType(this.hand, cardType.treasure));
  		this.money += treasures;
  		this.turnActions.push("Played " + treasures + " coins");

  		setTimeout(() => this.newHand(), this.speed);
  	}

  	private buy(card) {
  		if(this.money >= card.cost){
	  		this.money -= card.cost;
	  		this.deck.push(card);	
  		}
  		this.victory = this.addValues(this.getCardType(this.deck, cardType.victory));
  		if(this.victory > 50) {
  			this.press = true;
  		}
  	}

  	private prest() {
  		this.deck = [this.copper, this.copper, this.copper, this.copper, this.copper, this.copper, this.estate, this.estate, this.estate];
		this.speed /= 2;
		this.handSize++;
		this.prestiege++;
		this.actions++;
		this.turnCount = 0;
		this.money = 0;
		this.press = false;
  	}

}
enum cardType {
	treasure,
	action,
	victory
}